@font-face {
  src:
      url('Inter-Regular.woff') format('woff'),
      url('Inter-Regular.woff2') format('woff2'),
      url("Inter-Regular.ttf") format('truetype');
  font-family: 'Inter';
  font-weight: 400;
}
